import style from "./artemis.module.css";
import grid from './grid';

/**
 * casts raw classNames String to module classNames String
 * @param  {String} classNames the raw classNames
 * @returns {String} the transformed classNames
 */
const artemis = (classNames) => classNames.split(' ').map(c => style[c] || c).join(' ');

export { style, artemis,grid };